<template>
	<div id="member-change">
		<public-header title="会员充值"></public-header>
		<div class="content">
			<van-cell-group>
				<van-cell title="当前余额">
					<div slot="right-icon" class="c-danger">¥{{member.credit2}}</div>
				</van-cell>
				<van-cell title="当前积分">
					<div slot="right-icon" class="c-danger">{{member.credit1}}</div>
				</van-cell>
			</van-cell-group>
			<van-radio-group v-model="type" class="margin-10-t">
				<van-cell-group>
					<van-cell title="变动类型" />
					<van-cell title="余额" clickable @click="type = 'credit2'" class="border-0px">
						<van-radio name="credit2" />
					</van-cell>
					<van-cell title="积分" clickable @click="type = 'credit1'" class="border-0px">
						<van-radio name="credit1" />
					</van-cell>
				</van-cell-group>
			</van-radio-group>
			<van-radio-group v-model="change_type" class="margin-10-t">
				<van-cell-group>
					<van-cell title="变动方式" />
					<van-cell title="增加" clickable @click="change_type = 1" class="border-0px">
						<van-radio :name="1" />
					</van-cell>
					<van-cell title="减少" clickable @click="change_type = 2" class="border-0px">
						<van-radio :name="2" />
					</van-cell>
					<van-cell title="最终余额" clickable @click="change_type = 3" class="border-0px">
						<van-radio :name="3" />
					</van-cell>
				</van-cell-group>
			</van-radio-group>
			<van-cell-group class="margin-10-t">
				<van-field
					v-model="amount"
					clearable
					label="变动数目"
					placeholder="请输入变动数目"
					input-align="right"
				/>
				<van-field
					v-model="remark"
					label="备注"
					type="textarea"
					placeholder="请输入备注"
					rows="2"
					input-align="right"
				/>
			</van-cell-group>
			<div class="padding-15">
				<van-button size="normal" type="primary" :disabled="!islegal" block @click="onSubmit" class="font-16">提交</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				type: 'credit1',
				change_type: 1,
				amount: '',
				remark: '',
				islegal: false,
				showPreLoading: true,
				uid: 0,
				member: {
					credit2: 0,
					credit1: 0
				}
			}
		},
		methods: {
			onLoad() {
				var that = this;
				if(!that.uid) {
					this.util.$toast('会员uid不存在');
					return false;
				}
				getData({
					vue: that,
					url: 'plateform/member/list/change',
					autoAssign: true,
					variable: 'member',
					data: {
						uid: that.uid
					},
					success: (res) => {
						this.islegal = true;
					}
				})
			},
			onSubmit() {
				var that = this;
				if(!that.islegal) {
					return;
				}
				if(!that.amount) {
					that.util.$toast('请输入变动数目');
					return;
				}
				if(!that.remark) {
					that.util.$toast('请输入备注');
					return;
				}
				that.islegal = false;
				setData({
					vue: that,
					url: 'plateform/member/list/change',
					data: {
						uid: that.uid,
						type: that.type,
						change_type: that.change_type,
						amount: that.amount,
						remark: that.remark,
					},
					redirect: that.util.getUrl({path: 'pages/member/list'}),
					success: (res) => {
						that.util.$toast(res, that.util.getUrl({path: 'pages/member/list'}), '1000', 'replace');
						return;
					},
					fail: (res) => {
						this.util.$toast(res);
						this.islegal = true;
						return;
					}
				})
			}
		},
		created() {
			if(this.$route.query && this.$route.query.uid) {
				this.uid = this.$route.query.uid;
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
</style>


